@mixin addMargin1 {
  margin: 4px 0;
}
@mixin addMargin2 {
  margin-top: 4px;
}


.grid_data {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: 'ms hs ss sts''m h s st';
  grid-template-columns: 6fr 2fr 2.3fr 1fr;
  -moz-user-select: text;
  font-size: 13px;
  &:hover {

    &:first-child>div{
      background-color: rgba(45, 43, 43, 0.41);
      color: #ffffff;
      div{
        color: white;
      }

    }
  }

  .miner_s {
    grid-area: ms;
    @include addMargin1;
    padding: 10px;
    background-color: rgb(219, 219, 219);
    font-weight: bold;
    color: black;


    .miner {
      grid-area: m;
      @include addMargin1;

      color: black;
      font-weight: 400;


      .longMiner{
        font-size: 8px;
        &:hover{
          user-select: all;
          cursor: pointer;
          text-decoration: underline;
        }
        &::selection{
          background-color: black;
        }
      }

      .shortMiner{
        font-size: 15px;
        &:hover{
          user-select: all;
          cursor: pointer;
          text-decoration: underline;
        }
        &::selection{
          background-color: black;
        }
      }

    }

  }

  .hashrate_s {
    grid-area: hs;
    @include addMargin1;
    padding: 10px;
    background-color: rgb(219, 219, 219);
    color: black;
    font-weight: bold;

    .hashrate {
      grid-area: h;
      @include addMargin2;
      color: black;
      font-weight: 400;


    }

  }

  .shares_s {
    grid-area: ss;
    @include addMargin1;
    padding: 10px;
    background-color: rgb(219, 219, 219);
    font-weight: bold;
    color: black;

    .shares {
      grid-area: s;
      @include addMargin2;
      color: black;
      font-weight: 400;



    }

  }
  .status_s{
    grid-area: sts;
    @include addMargin1;
    padding: 10px;
    background-color: rgb(219, 219, 219);
    font-weight: bold;
    color: black;

    .status_offline{
      grid-area: st;
      @include addMargin2;
      color: black;
      font-weight: 400;

      span{
        background-color: #cb2410;
        border: 0.2px solid rgba(118, 110, 110, 0.17);
        border-radius: 5px;
        padding: 2px;
        font-weight: 400;
        color: white;
        }
      }



    .status_online{
      grid-area: st;
      @include addMargin2;
      color: black;
      font-weight: 400;

      span{
        background-color: #1ca00d;
        border: 0.2px solid rgba(118, 110, 110, 0.17);
        border-radius: 5px;
        padding: 2px;
        font-weight: 400;
        color: #ffffff;
      }
    }
  }

}